import { useState, useEffect } from 'react';
import { collection, query, getDocs, onSnapshot } from 'firebase/firestore';
import { db } from '../firebase'; // å‡è¨­ä½ çš„ firebase.js å°å‡ºäº† db

const useFirestoreCollection = (collectionName, queryConstraints = [], liveUpdate = false) => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    setLoading(true);
    setError(null); // Clear previous errors

    const collectionRef = collection(db, collectionName);
    let q = query(collectionRef, ...queryConstraints); // æ‡‰ç”¨å‚³å…¥çš„æŸ¥è©¢ç´„æŸ

    const fetchData = async () => {
      try {
        const querySnapshot = await getDocs(q);
        const fetchedData = querySnapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data(),
        }));
        setData(fetchedData);
        console.log(`useFirestoreCollection: Fetched data from ${collectionName}.`);
      } catch (err) {
        console.error(`useFirestoreCollection: Error fetching data from ${collectionName}:`, err);
        setError(`ç„¡æ³•è¼‰å…¥ ${collectionName} è³‡æ–™ï¼Œè«‹ç¨å¾Œå†è©¦ã€‚`);
      } finally {
        setLoading(false);
      }
    };

    const setupLiveListener = () => {
      const unsubscribe = onSnapshot(q, (querySnapshot) => {
        const fetchedData = querySnapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data(),
        }));
        setData(fetchedData);
        setLoading(false);
        setError(null);
        console.log(`useFirestoreCollection: Live update from ${collectionName}.`);
      }, (err) => {
        console.error(`useFirestoreCollection: Live update error for ${collectionName}:`, err);
        setError(`å³æ™‚æ›´æ–° ${collectionName} è³‡æ–™å¤±æ•—ã€‚`);
        setLoading(false);
      });
      return unsubscribe;
    };

    let unsubscribe = null;
    if (liveUpdate) {
      unsubscribe = setupLiveListener();
    } else {
      fetchData();
    }

    // Cleanup function for useEffect
    return () => {
      if (unsubscribe) {
        unsubscribe(); // Unsubscribe from live listener if it was set up
      }
    };
  }, [collectionName, queryConstraints, liveUpdate]); // ä¾è³´é …ï¼Œç•¶é€™äº›æ”¹è®Šæ™‚é‡æ–°åŸ·è¡Œ effect

  return { data, loading, error };
};

export default useFirestoreCollection;
